export default {
  data () {
    return {
      dialogAddVisible: false,
      dialogEditVisible: false
    }
  },
  methods: {
    onOpen () {
      let addForm = this.$refs['addForm']
      if (addForm !== undefined) {
        addForm.resetFields()
      }
    },
    onClose () {
      this.cancel()
    },
    reset () {
      this.form = JSON.parse(JSON.stringify(this.noneForm))
    },
    cancel () {
      this.reset()
      this.search(this.serverId || '')
      this.dialogAddVisible = false
      this.dialogEditVisible = false
    },
    handleAdd () {
      this.dialogAddVisible = true
      this.reset()
    },
    handleEdit (item) {
      this.dialogEditVisible = true
      this.form = item
    },
    handleSave () {
      this.$refs['addForm'].validate((valid) => {
        if (valid) {
          this.save()
        } else {
          return false
        }
      })
    },
    handleRemove (id) {
      this.$confirm('æ­¤æ“ä½œå°†æ°¸ä¹…åˆ é™¤, æ˜¯å¦ç»§ç»­?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        this.remove(id)
      }).catch(() => {})
    },
    showMessage (success, msg) {
      if (success) {
        this.$message({
          message: `æ­å–œä½ ï¼Œ${msg}æˆåŠŸ!`,
          type: 'success'
        })
        this.search(this.serverId || '')
        this.cancel()
      } else {
        this.$message({
          message: `å¯¹ä¸èµ·ï¼Œ${msg}å¤±è´¥!`,
          type: 'error'
        })
      }
    }
  }
}



