<template lang="pug">
  .detail-wrap
    .breadcrumb-wrap
      el-breadcrumb(separator="/")
        el-breadcrumb-item(v-bind:to="{ path: '/' }") é¦–é¡µ
        el-breadcrumb-item(v-bind:to="{ path: '/server' }") æœåŠ¡å™¨
        el-breadcrumb-item {{server.name}}

    el-alert(v-bind:title="`${server.name} - ${server.detail}`" type="success" v-bind:closable="false")
      .el-alert__description {{server.ip}}, {{server.outIp | FormatNull}}, {{server.user}}, {{server.pass}}
    .detail-button-wrap
      el-button(type="primary" icon="plus" v-on:click="handleAdd()") æ·»åŠ ç«¯å£

    .detail-table-wrap
      el-table(v-bind:data="tableData" border style="width: 100%")
        el-table-column(prop="name" label="åç§°" width="180")
        el-table-column(prop="port" label="ç«¯å£" width="180")
        el-table-column(prop="type" label="ç±»åž‹" width="180")
        el-table-column(prop="info" label="æè¿° ")
        el-table-column(label="æ“ä½œ" width="135")
          template(scope="scope")
            el-button(size="small" v-on:click="handleEdit(scope.row)") ç¼–è¾‘
            el-button(size="small" type="danger" v-on:click="handleRemove(scope.row.id)") åˆ é™¤

    el-dialog(title="æ·»åŠ ç«¯å£" v-model="dialogAddVisible" size="tiny" v-on:open="onOpen")
      el-form(v-bind:model="form" v-bind:rules="formRules" ref="addForm")
        el-form-item(label="åç§°" prop="name")
          el-input(v-model="form.name")
        el-form-item(label="ç«¯å£" prop="port")
          el-input(v-model="form.port")
        el-form-item(label="ç±»åž‹"  prop="type")
          el-input(v-model="form.type")
        el-form-item(label="æè¿°"  prop="info")
          el-input(type="textarea" v-model="form.info")
        el-form-item
          el-button(type="primary" v-on:click="handleSave()") ä¿å­˜
          el-button(v-on:click="cancel()") å–æ¶ˆ

    el-dialog(title="ç¼–è¾‘ç«¯å£" v-model="dialogEditVisible" size="tiny" v-on:close="onClose")
      el-form(v-bind:model="form" v-bind:rules="formRules" ref="editForm")
        input(type="hidden" v-model="form.id")
        el-form-item(label="åç§°")
          el-input(v-model="form.name" v-bind:disabled="true")
        el-form-item(label="ç«¯å£" prop="port")
          el-input(v-model="form.port")
        el-form-item(label="ç±»åž‹"  prop="type")
          el-input(v-model="form.type")
        el-form-item(label="æè¿°"  prop="info")
          el-input(type="textarea" v-model="form.info")
        el-form-item
          el-button(type="primary" v-on:click="editClick()") ä¿å­˜
          el-button(v-on:click="cancel()") å–æ¶ˆ
</template>
<script>
  import * as server from '../store/server'
  import formMixin from '../mixin/formMixin'

  let usertimer
  export default {
    data () {
      let validateName = (rule, value, callback) => {
        let reg = /^[a-zA-Z0-9-_]{1,20}$/
        if (value === '') {
          callback(new Error('è¯·è¾“å…¥åç§°'))
        } else {
          if (!reg.test(value)) {
            callback(new Error('ï¼ˆ1~20ä¸ªå­—ç¬¦ï¼ŒåŒ…å«å­—æ¯ã€æ•°å­—ã€ä¸‹åˆ’çº¿ã€æ¨ªçº¿ï¼‰'))
          } else {
            clearTimeout(usertimer)
            usertimer = setTimeout(() => {
              server.portCheckName({
                name: value
              }).then(res => {
                if (res.data.data === true) {
                  callback()
                } else {
                  callback(new Error('è¯¥åç§°å·²å­˜åœ¨'))
                }
              })
            }, 500)
          }
        }
      }

      let validatePort = (rule, value, callback) => {
        let reg = /^[0-9,]*$/
        if (value === '') {
          callback(new Error('è¯·è¾“å…¥ç«¯å£'))
        } else {
          if (!reg.test(value)) {
            callback(new Error('ï¼ˆå€¼èƒ½æ˜¯æ•°å­—,å¤šä¸ªä¹‹é—´ç”¨ , éš”å¼€ï¼‰'))
          } else {
            callback()
          }
        }
      }

      let validateType = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('è¯·è¾“å…¥ç±»åž‹'))
        } else {
          callback()
        }
      }

      let validateInfo = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('è¯·è¾“å…¥æè¿°'))
        } else {
          callback()
        }
      }
      return {
        serverId: '',
        server: {},
        noneForm: {
          id: '',
          name: '',
          port: '',
          type: '',
          info: '',
          serverId: ''
        },
        form: {
          id: '',
          name: '',
          port: '',
          type: '',
          info: '',
          serverId: ''
        },
        formRules: {
          name: [
            { validator: validateName, required: true, trigger: 'change' }
          ],
          port: [
            { validator: validatePort, required: true, trigger: 'change' }
          ],
          type: [
            { validator: validateType, required: true, trigger: 'change' }
          ],
          info: [
            { validator: validateInfo, required: true, trigger: 'change' }
          ]
        },
        tableData: []
      }
    },
    mixins: [formMixin],
    mounted () {
      this.serverId = this.$route.query.id
      this.getServer(this.serverId)
      this.search(this.serverId)
    },
    methods: {
      getServer (id) {
        server.serverSearchById({
          id: id
        }).then(res => {
          if (res.data.code === 200) {
            this.server = res.data.data
          }
        })
      },
      search (serverId) {
        server.portSearch({
          serverId: serverId
        }).then(res => {
          this.tableData = res.data.data
        })
      },
      save () {
        this.form.serverId = this.serverId
        server.portSave(this.form).then(res => {
          this.showMessage(res.data.data, 'ä¿å­˜')
        })
      },
      remove (id) {
        server.portRemove({
          id: id
        }).then(res => {
          this.showMessage(res.data.data, 'åˆ é™¤')
        })
      },
      editClick () {
        server.portUpdate(this.form).then(res => {
          this.showMessage(res.data.data, 'ä¿®æ”¹')
        })
      }
    }
  }
</script>
<style lang="sass">
  .detail-wrap
    .el-alert__title
      display: inline-block
      margin-top: 5px
      font-size: 18px
    .el-alert__description
      margin-top: 10px
      font-size: 16px
    .detail-button-wrap
      margin: 20px 0

</style>



